import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";
import { countries } from "./countries";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out required fields" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={"Information About your Current Spouse"}>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              
             
                <>
                  <h3 className="text-center mt-5" md={12}>
                    Provide the details of Your Prior Spouse
                  </h3>
                  <MDBRow>
                    <MDBCol md={4}>
                      <MDBInput
                        color="pink"
                        label="First Name"
                        group
                        type="text"
                        name="PriorSpousefirstName"
                        value={state.PriorSpousefirstName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol md={4}>
                      <MDBInput
                        color="pink"
                        label="Middle Name"
                        group
                        type="text"
                        name="PriorSpousemiddleName"
                        value={state.PriorSpousemiddleName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol md={4}>
                      <MDBInput
                        label="Last Name"
                        group
                        type="text"
                        name="PriorSpouselastName"
                        value={state.PriorSpouselastName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>

                  <MDBRow center className="mt-4">
                    <MDBCol md={4} className="align-text-start">
                      <h5 className="text-center">Immigration Status</h5>
                    </MDBCol>
                    <MDBCol>
                      <MDBRow>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="phonetype"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "immigrationStatusOfPriorSpouse",
                                "U.S Citizen"
                              );
                            }}
                            checked={
                              state.immigrationStatusOfPriorSpouse ==
                              "U.S Citizen"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            U.S Citizen{" "}
                          </label>
                        </div>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="phonetype"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "immigrationStatusOfPriorSpouse",
                                "Lawful Permanent Resident"
                              );
                            }}
                            checked={
                              state.immigrationStatusOfPriorSpouse ===
                              "Lawful Permanent Resident"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Lawful Permanent Resident{" "}
                          </label>
                        </div>

                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="phonetype"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "immigrationStatusOfPriorSpouse",
                                "other"
                              );
                            }}
                            checked={
                              state.immigrationStatusOfPriorSpouse ===
                              "other"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            other{" "}
                          </label>
                        </div>
                      </MDBRow>
                    </MDBCol>
                  </MDBRow>
                  <MDBRow>
                    {" "}
                    {state.immigrationStatusOfPriorSpouse == "other" && (
                      <MDBCol>
                        <MDBInput
                          color="pink"
                          label="Explain Other"
                          group
                          type="text"
                          name="immigrationStatusPriorSpouseOther"
                          value={state.immigrationStatusPriorSpouseOther}
                          onChange={(e) => {
                            validate();
                            onChangeField(e, e.target.name, e.target.value);
                          }}
                          containerClass="mb-0 mt-0"
                        />
                      </MDBCol>
                    )}
                  </MDBRow>
                  <MDBRow className="mt-3">
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Date of Birth"
                        group
                        type="Date"
                        name="dobPriorSpouse"
                        value={state.dobPriorSpouse}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0 mt-5"
                      />
                    </MDBCol>
                    <MDBCol className="mt-3">
                      <label>Spouse country of Citizenship</label>
                      <select
                        className="browser-default custom-select"
                        name="PriorSpouseCountryOfCitizenship"
                        value={state.PriorSpouseCountryOfCitizenship}
                        onChange={(e) => {
                          onChangeField("k", e.target.name, e.target.value);
                        }}
                        required
                      >
                        <option selected disabled>
                          Choose your option
                        </option>
                        {countries.map((country) => (
                          <option value={country.name}>{country.name}</option>
                        ))}
                      </select>
                    </MDBCol>
                    <MDBCol className="mt-3">
                      <label>Spouse country of Citizenship</label>
                      <select
                        className="browser-default custom-select"
                        name="PriorSpouseCountryOfBirth"
                        value={state.PriorSpouseCountryOfBirth}
                        onChange={(e) => {
                          onChangeField("k", e.target.name, e.target.value);
                        }}
                        required
                      >
                        <option selected disabled>
                          Choose your option
                        </option>
                        {countries.map((country) => (
                          <option value={country.name}>{country.name}</option>
                        ))}
                      </select>
                    </MDBCol>
                  </MDBRow>
                  <MDBRow className="mt-3">
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Date of Marriage with Prior Spouse"
                        group
                        type="Date"
                        name="dateOfMarriagePriorSpouse"
                        value={state.dateOfMarriagePriorSpouse}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0 mt-5"
                      />
                    </MDBCol>
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Date Marriage Ended"
                        group
                        type="Date"
                        name="dateMarriageEndedPriorSpouse"
                        value={state.dateMarriageEndedPriorSpouse}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0 mt-5"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow center className="mt-4">
                    <MDBCol md={6} className="align-text-start">
                      <h5 className="text-center">
                        How Current Spouse Marriage Ended
                      </h5>
                    </MDBCol>
                    <MDBCol>
                      <MDBRow>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="marraigeEnded"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "PriorSpouseMarriageEndedHow",
                                "Anulled"
                              );
                            }}
                            checked={
                              state.PriorSpouseMarriageEndedHow === "Anulled"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Anulled{" "}
                          </label>
                        </div>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="marraigeEnded"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "PriorSpouseMarriageEndedHow",
                                "Divorced"
                              );
                            }}
                            checked={
                              state.PriorSpouseMarriageEndedHow === "Divorced"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Divorced{" "}
                          </label>
                        </div>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="marraigeEnded"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "PriorSpouseMarriageEndedHow",
                                "Spouse Deceased"
                              );
                            }}
                            checked={
                              state.PriorSpouseMarriageEndedHow ===
                              "Spouse Deceased"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Spouse Deceased{" "}
                          </label>
                        </div>

                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="marraigeEnded"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "PriorSpouseMarriageEndedHow",
                                "other"
                              );
                            }}
                            checked={
                              state.PriorSpouseMarriageEndedHow === "other"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            other{" "}
                          </label>
                        </div>
                      </MDBRow>
                      <MDBRow>
                        {" "}
                        {state.PriorSpouseMarriageEndedHow == "other" && (
                          <MDBCol>
                            <MDBInput
                              color="pink"
                              label="Explain Other"
                              group
                              type="text"
                              name="PriorSpouseMarriageEndedHowOther"
                              value={state.PriorSpouseMarriageEndedHowOther}
                              onChange={(e) => {
                                validate();
                                onChangeField(e, e.target.name, e.target.value);
                              }}
                              containerClass="mb-0 mt-0"
                            />
                          </MDBCol>
                        )}
                      </MDBRow>
                    </MDBCol>
                  </MDBRow>
                </>
              
            </MDBCol>
          </MDBRow>
        
            <MDBRow center className="d-flex flex-column align-items-center">
              {touched && errorMsg.length > 0 && (
                <div className="text-danger mb-2">{errorMsg}</div>
              )}
              {touched && !errorMsg && (
                <Link to={nextStep}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                </Link>
              )}
              {(!touched || errorMsg) && (
                <MDBBtn
                  color={"grey"}
                  disabled
                  rounded
                  type="button"
                  className="z-depth-1a"
                >
                  Next
                </MDBBtn>
              )}
            </MDBRow>
          
        </form>
      </MDBContainer>
    </Step>
  );
}
