import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out required fields" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={"Other Names you have used since birth"}>
      <h6 className="text-center">
        (include nicknames and maiden names if applicable)
      </h6>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="First Name"
                    group
                    type="text"
                    name="additionalFirstName"
                    value={state.additionalFirstName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="Middle Name"
                    group
                    type="text"
                    name="additionalMiddleName"
                    value={state.additionalMiddleName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    label="Last Name"
                    group
                    type="text"
                    name="additionalLastName"
                    value={state.additionalLastName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <h3 className="text-center mt-5">
                Would you like to legally change you Name?
              </h3>
              <MDBRow center>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "legallyChangeName", "Yes");
                    }}
                  >
                    Yes
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "legallyChangeName", "No");
                      history.push(nextStep);
                    }}
                  >
                    No
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
              {state.legallyChangeName == "Yes" && (
                <>
                  <h3 className="text-center mt-5">
                    Your Name Exactly as it appears on Permanent Resisdent Card
                  </h3>
                  <MDBRow>
                    <MDBCol md={4}>
                      <MDBInput
                        color="pink"
                        label="First Name"
                        group
                        type="text"
                        name="newFirstName"
                        value={state.newFirstName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol md={4}>
                      <MDBInput
                        color="pink"
                        label="Middle Name"
                        group
                        type="text"
                        name="newMiddleName"
                        value={state.newMiddleName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol md={4}>
                      <MDBInput
                        label="Last Name"
                        group
                        type="text"
                        name="newLastName"
                        value={state.newLastName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>

                  <MDBRow
                    center
                    className="d-flex flex-column align-items-center"
                  >
                    {touched && errorMsg.length > 0 && (
                      <div className="text-danger mb-2">{errorMsg}</div>
                    )}
                    {touched && !errorMsg && (
                      <Link to={nextStep}>
                        <MDBBtn
                          color="pink"
                          rounded
                          type="button"
                          className="z-depth-1a"
                        >
                          Next
                        </MDBBtn>
                      </Link>
                    )}
                    {(!touched || errorMsg) && (
                      <MDBBtn
                        color={"grey"}
                        disabled
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    )}
                  </MDBRow>
                </>
              )}
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
